<template lang="pug">
  .level
    .level-left
    .level-right
      .level-item
        .tags.are-medium
          .tag(v-for="(item, i) in currencies"
            :key="i"
            :class="{ 'is-active': currency == item }"
            @click="changeCurrency(item)"
          ) {{ item }}

</template>

<script>
import { mapActions, mapGetters } from "vuex";

export default {
  name: "Currencies",
  computed: {
    ...mapGetters(["currencies", "currency"])
  },
  methods: {
    ...mapActions(["changeCurrency"])
  }
};
</script>
