<template lang="pug">
  
  Widgets(:stats="priceStats")

</template>

<script>
import Widgets from "@/components/common/Widgets";
import { mapGetters } from "vuex";

export default {
  name: "PriceTab",
  components: { Widgets },
  computed: {
    ...mapGetters(["stats", "currency", "courses"]),
    priceStats() {
      return [
        {
          label: this.$translate.text("Price"),
          numbers: [
            {
              number: this.courses['USD'].value,
              suffix: "USD"
            },
            {
              prefix: " / ",
              number: 100,
              convertToFiat: true
            }
          ],
          customClass: "is-12-mobile is-6-tablet"
        },
        {
          label: this.$translate.text("The price of the dollar in UMI"),
          value: '1 USD = ' + (1 / (this.courses['USD'].value)).toFixed(2) + ' UMI',
          customClass: "is-12-mobile is-6-tablet"
        },
        {
          label: this.$translate.text("Trading volume on exchanges in 24 hours"),
          labelLink: 'https://umi.top/volumes',
          numbers: [
            {
              number: this.stats.volume_all,
              suffix: "UMI"
            },
            {
              prefix: " / ",
              number: this.stats.volume_all,
              convertToFiat: true,
              suffix: this.currency
            }
          ],
          customClass: "is-12-mobile is-6-tablet is-4-desktop"
        },
        {
          label: this.$translate.text("P2P trading volume on exchanges in 24 hours"),
          labelLink: 'https://umi.top/volumes',
          numbers: [
            {
              number: this.stats.volume_p2p,
              suffix: "UMI"
            },
            {
              prefix: " / ",
              number: this.stats.volume_p2p,
              convertToFiat: true,
              suffix: this.currency
            }
          ],
          customClass: "is-12-mobile is-6-tablet is-4-desktop"
        },
        {
          label: this.$translate.text("Liquidity") + ' (' + this.$translate.text("buy") + '/' + this.$translate.text("sell") + ')',
          labelLink: 'https://umi.top/liquidity',
          numbers: [
            {
              value: this.stats.liquid ? this.convert(this.stats.liquid.bids[this.currency], this.currency) : 0,
              suffix: '/'
            },
            {
              value: this.stats.liquid ? this.convert(this.stats.liquid.asks[this.currency], this.currency) : 0,
            }
          ],
          customClass: "is-12-mobile is-narrow-tablet-only is-4-desktop"
        },
        {
          label: this.$translate.text("Maximum price for 24 hours"),
          numbers: this.stats.prices.high24hr,
          customClass: "is-12-mobile is-6-tablet is-6-desktop"
        },
        {
          label: this.$translate.text("Minimum price for 24 hours"),
          numbers: this.stats.prices.low24hr,
          customClass: "is-12-mobile is-6-tablet is-6-desktop"
        }
      ];
    }
  }
};
</script>
