<template lang="pug">
  .structures
    .content
      .is-size-4(v-translate) UMI structures
    .structure.secondary(v-for="(item, i) in structures" :key="i")
      .columns.is-multiline.is-flex-mobile.is-vcentered
        .column.is-narrow.is-narrow-mobile
          .icon.custom-icon(v-if="item.logo" :style="'background-image: url(data:image/svg+xml;base64,' + item.logo + ')'")
            img(v-if="item.prefix == 'roy'" v-webp="require('@/assets/structures/roy.png')")
        .column.is-2.is-10-mobile
          .name {{ item.name }}
        .column
          .description(v-if="item.description") {{ t(item.description) }}
        //.column.is-narrow-mobile
          .balance.is-ultralight(:class="{'is-invisible': !item.balance}") {{ t('Balance') }} <span class="green">{{ toUmi(item.balance) }}</span>
        .column.is-narrow.is-12-mobile(v-if="item.url")
          a.has-text-link(:href="item.url" target="_blank") {{ t('Structure site') }}
            | 
            .icon.is-small
              SvgIcon.icon(name="open-in-new")

      Widgets(:stats="stats(item)")

      .colored.blocks.columns.is-centered.has-text-centered.is-flex-mobile.is-multiline
        .column.is-12-mobile.has-text-left
          router-link.block(:to="{ name: 'Address', params: { address: item.profit_address }}")
            .is-ultralight(v-translate) Profit Wallet
            span.green {{ cut(item.profit_address, 10) }}
        .column.is-12-mobile.has-text-left
          router-link.block(:to="{ name: 'Address', params: { address: item.fee_address }}")
            .is-ultralight(v-translate) Fee Wallet
            span.has-text-link {{ cut(item.fee_address, 10) }}
        .column.is-12-mobile.has-text-left
          template(v-if="item.transit_addresses && item.transit_addresses.length === 1")
            .block
              .is-ultralight(v-translate) Transit Wallet
              router-link(
                v-for="(transit, i) in item.transit_addresses"
                :key="i"
                :to="{ name: 'Address', params: { address: transit }}"
              )
                span.has-text-warning {{ cut(transit, 10) }}
          b-collapse(
            v-else-if="item.transit_addresses && item.transit_addresses.length > 0"
            class="block"
            :open="false"
            @open="toggleOpen(item.prefix)"
            @close="toggleOpen(item.prefix)"
          )
            div(
              slot="trigger"
              slot-scope="props"
              class="toggler"
              role="button"
            )
              .is-ultralight(v-translate) Transit Wallet
              div
                .has-text-warning {{ item.transit_addresses.length + ' ' + t('addresses') }}
              .icon
                SvgIcon(:name="isOpen[item.prefix] ? 'chevron-down' : 'chevron-up'")
            .content
              ul
                li(v-for="(transit, i) in item.transit_addresses")
                  router-link(:to="{ name: 'Address', params: { address: transit }}")
                    span.has-text-warning {{ cut(transit, 10) }}

</template>

<script>
import Widgets from "@/components/common/Widgets";
import { mapGetters } from "vuex";
import SvgIcon from "@/components/common/SvgIcon";

export default {
  name: "StructuresTab",
  data() {
    return {
      filter: "all",
      time_delta: 0,
      isOpen: []
    };
  },
  components: { Widgets, SvgIcon },
  computed: {
    ...mapGetters(["structures"]),
    count() {
      return this.structures.length;
    }
  },
  methods: {
    toggleOpen(id) {
      this.isOpen[id] = !this.isOpen[id]
    },
    staking(percent) {
      let periods = 30; // количество периодов капитализации. 30 - раз в день, 30 * 24 = 720 - раз в час
      let effective_interest = parseInt(percent); // 15% - Эффективная МЕСЯЧНАЯ ставка. Месяц у нас равен 30 календартным дням.
      let nominal_interest = periods * ((1 + (effective_interest / 10000)) ** (1 / periods) - 1);
      return {
        day: nominal_interest / 30 * 100,
        month: parseInt(percent) / 100
      }
    },
    coefficient(deposit_percent) {
      const effective_interest = deposit_percent;
      const periods = 30 * 24 * 60 * 60 * 10;
      return (1 + effective_interest / 10000) ** (1 / periods);
    },
    growInit() {
      const start = new Date();
      this.timer = setInterval(() => {
        this.time_delta = new Date() - start;
      }, 100);
    },
    stats(structure) {
      return [
        {
          label: this.$translate.text("Staking per day"),
          value: (structure.deposit_percent > 0 ? (this.staking(structure.deposit_percent).day.toFixed(3)) : 0) + '%',
          customClass: "is-narrow is-6-mobile"
        },
        {
          label: this.$translate.text("Staking per month"),
          value: (structure.deposit_percent > 0 ? (this.staking(structure.deposit_percent).month) : 0) + '%',
          customClass: "is-narrow is-6-mobile"
        },
        {
          label: this.$translate.text("Fee"),
          number: structure.fee_percent / 100,
          suffix: "%",
          customClass: "is-narrow is-6-mobile"
        },
        {
          label: this.$translate.text("Coins"),
          number: structure.balance * this.coefficient(structure.deposit_percent) ** (this.time_delta / 100),
          suffix: "UMI",
          customClass: "is-6-mobile"
        },
        {
          label: this.$translate.text("Members"),
          number: structure.address_count,
          customClass: "is-narrow is-6-mobile"
        },
        {
          label: this.$translate.text("Structure Income"),
          number: structure.profit_percent / 100,
          suffix: "%",
          customClass: "is-narrow is-6-mobile"
        }
      ];
    }
  },
  created() {
    this.growInit();
  }
};
</script>

