<template lang="pug">
  .transaction.secondary(v-if="trx")
    .level
      .level-left
        h4(v-if="trx.value && trx.version <= 1")
          | {{ toUmi(amount) }} UMI
          | 
          span.is-ultralight ≈ {{ pretty(amount) }}
        h4(v-else) {{ action }}
          | 
          span.is-ultralight(v-if="amount > 0") {{ toUmi(amount) }} UMI 
          
      .level-right.confirmations
        SvgIcon.icon(:name="trx.confirmed_at ? 'green-check' : 'red-check'")
        .is-ultralight {{ trx.confirmed_at | moment("DD.MM.YY HH:mm:ss [GMT] Z") }}
    
    .columns.is-multiline.is-variants.is-1
      .column.is-7
        .columns.is-multiline.is-flex-mobile
          .column.is-12-mobile
            .from
              router-link.address.red(:to="{ name: 'Address', params: { address: trx.sender }}") {{ trx.sender }}
          .column.is-12-mobile
            .columns.is-vcentered.is-flex-mobile
              .column.is-narrow.is-narrow-mobile
                SvgIcon.arrow(v-if="trx.recipient" name="arrow-right")
              .column
                .amount(v-if="trx.fee_address")
                  | {{ toUmi(trx.value) }} UMI 
                  span.is-ultralight ≈ {{ pretty(trx.value) }}
                router-link.address.green(:to="{ name: 'Address', params: { address: trx.recipient }}") {{ trx.recipient }}
            .columns.is-vcentered.is-flex-mobile(v-if="trx.fee_address")
              .column.is-narrow.is-narrow-mobile
                SvgIcon.arrow(name="arrow-right")
              .column
                .amount
                  | {{ toUmi(trx.fee_value) }} UMI 
                  span.is-ultralight ≈ {{ pretty(trx.fee_value) }}
                router-link.address.green(:to="{ name: 'Address', params: { address: trx.fee_address }}") {{ trx.fee_address }}

      .column
        .columns.is-vcentered.is-flex-mobile
          .column.has-text-right
            router-link.hash.is-ultralight(:to="{ name: 'Transaction', params: { transaction: trx.hash }}") tx: {{ trx.hash }}
          .column.is-narrow.is-narrow-mobile
            .buttons
              button.button.is-primary.is-small(:title="t('Copy tx')" v-clipboard:copy="$blockchain + 'transaction/' + trx.hash" v-clipboard:success="onCopy")
                SvgIcon.icon(name="content-copy-white")
              router-link.button.is-primary.is-small.has-text-link(:to="{ name: 'Transaction', params: { transaction: trx.hash }}" :title="t('Open in new window')" target="_blank")
                SvgIcon.icon(name="open-in-new")
        .columns.is-vcentered.is-flex-mobile
          .column.has-text-right
            router-link.is-ultralight(:to="{ name: 'Block', params: { height: trx.block_height }}") {{ t('Block') }}: {{ trx.block_height }}
          .column.is-narrow.is-narrow-mobile
            .buttons
              button.button.is-primary.is-small(:title="t('Copy block height')" v-clipboard:copy="trx.block_height" v-clipboard:success="onCopy")
                SvgIcon.icon(name="content-copy-white")
              router-link.button.is-primary.is-small.has-text-link(:to="{ name: 'Block', params: { height: trx.block_height }}" :title="t('Open in new window')" target="_blank")
                SvgIcon.icon(name="open-in-new")


</template>

<script>
import SvgIcon from "@/components/common/SvgIcon";

export default {
  name: "Transaction",
  components: { SvgIcon },
  props: {
    trx: {
      type: Object,
      default: null
    }
  },
  computed: {
    amount() {
      return parseInt(this.trx.value || 0) + parseInt(this.trx.fee_value || 0);
    },
    action() {
      switch (this.trx.version) {
        case 2:
          return (
            this.$translate.text("Create new structure") +
            " " +
            this.trx.struct.name
          );
        case 3:
          return this.$translate.text("Update structure");
        case 4:
          return this.$translate.text("Set profit address");
        case 5:
          return this.$translate.text("Set fee address");
        case 6:
          return this.$translate.text("Activate transit address");
        case 7:
          return this.$translate.text("Deactivate transit address");
        default:
          return this.$translate.text("Another operation");
      }
    }
  }
};
</script>

<style scoped lang="scss">
.transaction {
  margin-bottom: 0.25rem;
  padding: 1.5rem;

  h4 {
    color: #0d95e1;
    margin-right: 0.5rem;
  }
}

.confirmations {
  .icon {
    margin-right: 0.5rem;
  }
}

.arrow {
  width: 1rem;
  height: 0.55rem;
}

.hash {
  max-width: 22rem;
  display: inline-block;
}
</style>
